* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;

}
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    color: #ffffff;
    background-color: #000000;
  }
  .App
  {
    overflow-y: hidden;
  /* overflow-x: hidden; */
    width: 100%;
    position: relative;
  }
  
  .section {
    padding: 100px 20px;
    overflow: hidden;
  }
  
  .black {
    background-color: #000000;
  }
  
  .yellow {
    background-color: #debd20;
  }
  
  .red {
    background-color: #b33f4c;
  }

  .all-sections-content ::selection {
    color: #000;
    background: #debd20;
  }

  .section.yellow ::selection {
    color: #fff;
    background: #b33f4c;
  }

  .section.red ::selection {
    color: #000;
    background: #debd20;
  }

  #about-me
  {
    padding: 0px 20px;
  }
  
