  .hover_effect
  {
    pointer-events: none;
  }
  .hover_effect_shapes {
    position: absolute;
    height: 100vh;
    width: 100vw;
    /* background: #2128bd; */
    opacity: 0;
    overflow: hidden;
    
  }
  .hover_effect_shape {
    will-change: transform;
    position: absolute;
    border-radius: 50%;
    z-index: 99999;
  }
