.menu {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100vh;
  background-color: #fff;

  flex-direction: column;
  justify-content: flex-start;
  display: flex;
  -webkit-user-select: none;
  user-select: none;
  color: #000;
  /* overflow: hidden; */
  z-index: 5;

  counter-reset: menucounter;

  transition-property: all;
	transition-duration: 1s;
	transition-timing-function: cubic-bezier(0, 0.1, 1, 0);
}

.menu_container
{
  position: relative;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: flex-start;
  display: flex;

}
.menu-bottom-img {
  width: 100vw;
  height: 11.4vw;
  transform-origin: 50% 0;
  background-image: url(./images/menu-bottom.svg);
  background-position: 50% 0;
  background-size: cover;
  position: absolute;
  transform: perspective(1000px);
  bottom: -11.4vw;
}

.hide-menu {
  top: -130%;
}
.nav-divider {
  transition-property: height;
	transition-duration: 1.2s;
  transition-delay: calc(0.03s * var(--number));
  transition-timing-function: ease-out(0, 0.1, 1, 0);
}
.menu-item
{
  transition-property: opacity, top;
	transition-duration: 0.7s;
  transition-delay: calc(0.05s * var(--number));
  transition-timing-function: ease-out;
  opacity: 1;
  top: 0px;
}
.hide-menu .menu-item
{
  opacity: 0;
  top: -700px;
}

.hide-menu .nav-divider
{
  height: 0px;
}


.site-name
{
  position: fixed;
  color: #000;
  top:20px;
  left: 20px;
  font-size: 2rem;
  z-index: 7;
  user-select: none;
  cursor: default;
}
.site-name.menu-displayed{
  color: #fff;
}

.site-name-part2{
  position: relative;
  top:-10px;
  left:50px;
}

.menu-btn{
  position: fixed;
  color: #000;
  top:20px;
  font-size: 2rem;
  cursor: pointer;

  transition: all 0.3s;
  z-index: 6;
  right: -20px;
}

.menu-btn.menu-displayed{
  color: #fff;
}

.menu-btn div{
  display: inline-block;
}

.menu-btn > .menu-btn-effect > span:nth-child(1) span{
  will-change: transform;
  transform-style: preserve-3d;
  transition: 0.5s, opacity 0.3s;
  transition-delay: calc(0.02s * var(--index));
  transform-origin: bottom;
  display: inline-block;

  transform: translate3d(0, -100%, 0) rotateX(-90deg);

  
  opacity: 0;
}


.menu-btn:hover > .menu-btn-effect > span:nth-child(1) span {
  transform: translate3d(0, 0%, 0) rotateX(0deg);
  
  opacity: 1;
}

.menu-btn:hover{
  right: 20px;
}













