.nav-txt-wrap {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: flex-end;
  /* align-items: center; */
  padding-bottom: 7.6vh;
  display: flex;
  overflow: hidden;
}

.nav-90-div {
  perspective: 40em;
  perspective-origin: 50%;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  position: relative;
  transform: rotate(-90deg);
}

.menu-item-text {
  pointer-events: none;
  display: block;
  position: relative;
}

.nav-divider {
  width: 2px;
  height: 100%;
  background-color: #000;
  flex: none;
}

.menu-item > div {
  overflow: hidden;
}

.menu-item > div:nth-child(1) span {
  will-change: transform;
  transform-style: preserve-3d;
  transition: 0.5s, opacity 0.3s;
  transition-delay: calc(0.02s * var(--index));
  transform-origin: bottom;
  display: inline-block;
}

.menu-item > div:nth-child(2) span {
  will-change: transform;
  transform-style: preserve-3d;
  transition: 0.5s, opacity 0.3s;
  transition-delay: calc(0.02s * var(--index));
  transform-origin: top;
  display: inline-block;
  transform: translate3d(0, 100%, 0) rotateX(-115deg);
  opacity: 0;
}

.menu-item:hover > div:nth-child(1) span {
  transform: translate3d(0, -100%, 0) rotateX(-115deg);
  opacity: 0;
}

.menu-item:hover > div:nth-child(2) span {
  transform: translate3d(0, 0%, 0) rotateX(0deg);
  opacity: 1;
}

